.quiz__answer-item {
  margin: 0.5rem;
  padding: 1rem;
  flex: 0 0 33%;
  background-color: #ffffff;
  color: #000000;
  box-shadow: 0 4px 4px 0 #fcfcfcec, 0 3px 5px 0 #a16c6c;
}

.btn-element {
  margin-right: 1rem;
  transform:scale(1.7);
  opacity:0.9;
  cursor:pointer;
}

@media (max-width: 568px) {
  .quiz__answer-item {
    flex: 0 0 90%;
  }
}

@media (max-width: 425px) {
  .quiz__answer-item {
    flex: 0 0 90%;
  }
}